import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";
let _this,timer;
export default {
	components:{
		uniLoadMore
	},
	computed:{
	   i18n() {  
	     return this.$i18nMsg()  
	   }
	},
	data() {
		return {
			record:[],
			loadingType: 'more',
			account: '',
			pageNum: 1,
			pageSize: 20,
			flowType:['储能池分红'],
		};
	},
	onLoad:function(options){
		_this = this
		uni.setNavigationBarTitle({
		            title:this.$i18nMsg().ecologyDetail.types['4']
		        });
		this.account = options.account
		this.fetchList()
	},
	onReachBottom:function(){
		 if (timer != null) {
		            clearTimeout(timer);
		        }
		   timer = setTimeout(function() {
		        _this.fetchMoreList();
		        }, 1000);
	},
	methods:{
		async fetchMoreList(){
			if (this.loadingType ==''){
				return 
			}
			this.pageSize += 20
			let data ={
				pageNum: this.pageNum,
				pageSize: this.pageSize
				
			}
			const res = await this.$http.get('/deal/energy_flow',{data: data})
			if (res.data.data.isLastPage){
				this.loadingType = ''
			}
			this.record = res.data.data.list
			this.record.forEach((item)=>{
				item.createTime = this.handleDate(item.createTime)
				// if (item.details == '推荐奖'){
				// 	item.details = '動力A'
				// }
				// if (item.details == '对碰奖'){
				// 	item.details = '動力B'
				// }
				// if (item.details == '领导奖'){
				// 	item.details = '動力C'
				// }
				// if (item.details == '储能池分红'){
				// 	item.details = '储能池释放'
				// }
			})
			console.log(res)
		},
		async fetchList(){
			let data ={
				pageNum: this.pageNum,
				pageSize: this.pageSize
				
			}
			const res = await this.$http.get('/deal/energy_flow',{data: data})
			
			this.record = res.data.data.list
			this.record.forEach((item)=>{
				item.createTime = this.handleDate(item.createTime)
				// if (item.details == '推荐奖'){
				// 	item.details = '動力A'
				// }
				// if (item.details == '对碰奖'){
				// 	item.details = '動力B'
				// }
				// if (item.details == '领导奖'){
				// 	item.details = '動力C'
				// }
				// if (item.details == '储能池分红'){
				// 	item.details = '储能池释放'
				// }
			})
			console.log(res)
		},
		handleDate:function(d){
			let arr = d.split(' ')
			let date1 = arr[0].split('-')
			let date2 = arr[1].split(':')
			
			return date1[1]+'-'+date1[2]+' '+date2[0]+':'+date2[1]
		},
	}
}
